import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Switch
} from 'react-router-dom'
import './App.css';

import Login from './Login/Login';
import Home from './Home/Home';

// import Dashboard from './Home/Dashboard';
import Invoices from './Home/Invoices/Invoices';
import Timesheets from './Home/Timesheets/Timesheets';

import ServiceOrders from './Home/ServiceOrders/ServiceOrders';
import ServiceOrderDetails from './Home/ServiceOrders/ServiceOrderDetails/ServiceOrderDetails';

const About = () => (
  <div>
    <h2>About</h2>
  </div>
)

const NotFound = () => (
  <div>Not Found</div>
)

const App = () => (
  <Router>
    <div>
      <Switch>
        <Route exact path='/' component={Home}/> {/* Change to Login for server integration */}
        <Route path='/home' component={Home}/>
        <Route path='*' component={Home}/>
      </Switch>
    </div>
  </Router>
)

export default App;



