import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  NavLink
} from 'react-router-dom';
import Dashboard from './Dashboard/Dashboard';
import ServiceOrders from './ServiceOrders/ServiceOrders';
import ServiceOrderDetails from './ServiceOrders/ServiceOrderDetails/ServiceOrderDetails';
import CreateServiceOrder from './ServiceOrders/CreateServiceOrder/CreateServiceOrder';
import Invoices from './Invoices/Invoices';
import InvoiceDetails from './Invoices/InvoiceDetails/InvoiceDetails';
import Timesheets from './Timesheets/Timesheets';
import logoBlack from '../../assets/commbank_logo_black.png';
import userIcon from '../../assets/user1.png';

import help from '../../assets/help.png';
import notification from '../../assets/header_notification.png';
import search from '../../assets/header_search.png';
import contact from '../../assets/header_contact.png';
import logout from '../../assets/header_notification.png';
import './Home.css';

const Navbar = () => (
  <div className="navbar">
    <div className="welcome">
      <img src={userIcon} className="welcomeIcon" alt="userIcon" />
      <NavLink to={'/home'} className="welcomeText">Welcome, Jeanine!</NavLink>
    </div>
    <div className="menu">
      <div className="menuItem">
        <img src={notification} alt="notification" className="menuIcon" />
        <div className="menuText">Notifications</div>
      </div>
      <div className="menuItem">
        <img src={search} alt="notification" className="menuIcon" />
        <div className="menuText">Search</div>
      </div>
      <div className="menuItem">
        <img src={contact} alt="notification" className="menuIcon" />
        <div className="menuText">Contact</div>
      </div>
      <div className="menuItem">
        <img src={help} alt="notification" className="menuIcon" />
        <div className="menuText">Help</div>
      </div>
      <div className="menuItem menuLogout">
        <div className="menuText">Log out</div>
      </div>
    </div>
    <div className="links">
      <img src={logoBlack} alt="logoBlack" className="linksLogo" />
      <NavLink activeClassName="linkActive" to={'/home/service_orders'} className="link">
        Service Orders
      </NavLink>
      <NavLink activeClassName="linkActive" to={'/home/invoices'} className="link">
        Invoices
      </NavLink>
      <NavLink activeClassName="linkActive" to={'/home/timesheets'} className="link">
        Timesheets
      </NavLink>
    </div>
  </div>
)

const Home = ({ match }) => (
    <div>
      <Navbar />
      <Route exact path={'/'} component={Dashboard} />
      <Route exact path={'/home'} component={Dashboard} />
      <Route exact path={'/home/service_orders'} component={ServiceOrders} />
      <Route exact path={'/home/service_orders/view/:serviceOrderId'} component={ServiceOrderDetails} />
      <Route exact path={'/home/service_orders/create'} component={CreateServiceOrder} />
      <Route exact path={'/home/invoices'} component={Invoices} />
      <Route exact path={'/home/invoices/:invoiceId'} component={InvoiceDetails} />
      <Route exact path={'/home/timesheets'} component={Timesheets} />
    </div>
)

export default Home;



