import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Link,
  NavLink
} from 'react-router-dom';
import './ServiceOrders.css';
import detailsIcon from '../../../assets/details.png';
import ServiceOrderDetails from './ServiceOrderDetails/ServiceOrderDetails';

class ServiceOrders extends React.Component {
  constructor() {
    super();
    this.state = {
      data: []
    };
  }

  componentWillMount() {
    //TODO: AJAX Call to fetch service order list
    let data = [
      {
      "serviceOrder" : {
        "status" : "Pending",
        "serviceOrderId" : "SO0",
        "purchaseOrderId" : "PO0",
        "supplierId" : "SUPP8888",
        "supplier" : "DXC",
        "projectName" : "Test1",
        "pricingType" : "FIXED_PRICE",
        "price" : 3200000,
        "ppmcCode" : 12345,
        "projectManager" : "Peter Smith",
        "estimatedCharges" : [{
          "role" : "Java Developer",
          "roleType" : "Onshore",
          "resource" : "Max Planck",
          "startDate" : "1494210646690",
          "endDate" : "1494810646690",
          "cost" : 1000000,
          "hours" : 10,
          "total" : 2000000
        }]
      }},
      {
      "serviceOrder" : {
        "status" : "Approved",
        "serviceOrderId" : "SO1",
        "purchaseOrderId" : "PO1",
        "supplierId" : "SUPP8888",
        "supplier" : "DXC",
        "projectName" : "Test1",
        "pricingType" : "FIXED_PRICE",
        "price" : 3200000,
        "ppmcCode" : 12345,
        "projectManager" : "Peter Smith",
        "estimatedCharges" : [ {
          "role" : "Java Developer",
          "roleType" : "Onshore",
          "resource" : "Max Planck",
          "startDate" : "1494910646690",
          "endDate" : "1495210646690",
          "cost" : 1000000,
          "hours" : 10,
          "total" : 2000000
        }]
      }
    }];
    this.setState({ data: data });
  }

  render() {
    console.log(this.state.data);
    return(
      <div className="section">
        <div className="sectionTitle">Service Orders</div>
        <table className="SO-table">
          <tbody>
            <tr className="SO-tableHeaders">
              <th className="SO-tableHeader">WO Number</th>
              <th className="SO-tableHeader">Name</th>
              <th className="SO-tableHeader">Status</th>
              <th className="SO-tableHeader">PO Number</th>
              <th className="SO-tableHeader"></th>
            </tr>
            {
              this.state.data.map(function(d, i) {
                let serviceOrderId = d.serviceOrder.serviceOrderId;
                return <tr className="SO-tableRows" key={d.serviceOrder.serviceOrderId}>
                    <td className="SO-tableRow">{d.serviceOrder.serviceOrderId}</td>
                    <td className="SO-tableRow">{d.serviceOrder.projectName}</td>
                    <td className="SO-tableRow">{d.serviceOrder.status}</td>
                    <td className="SO-tableRow">{d.serviceOrder.purchaseOrderId}</td>
                    <td className="SO-tableRow">
                      <div className="SO-details">
                        <img src={detailsIcon} className="SO-detailsIcon" />
                        <NavLink to={'service_orders/view/'+serviceOrderId} className="SO-detailsText">DETAILS</NavLink>
                      </div>
                    </td>
                  </tr>
              })
            }
          </tbody>
        </table>
        <Link to={'service_orders/create'} className="SO-createSo">
          Create New Service Order
        </Link>
      </div>
    )
  }
}

export default ServiceOrders;



