import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Link,
  NavLink
} from 'react-router-dom';

import backIcon from '../../../../assets/back.png';
import approvedIcon from '../../../../assets/approved.png';
import pendingIcon from '../../../../assets/pending.png';
import onholdIcon from '../../../../assets/onhold.png';

import './InvoiceDetails.css';

class InvoiceDetails extends React.Component {
  constructor() {
    super();
    this.state = {
      data: []
    };
  }

  componentWillMount() {
    //ajax call to get service order by ID
    let data = {
      "invoice" : {
        "invoiceId": "IN123",
        "name": "ABC Consulting",
        "date": "25/03/2017",
        "status": "Processed",
        "purchaseOrders": [
          { "purchaseOrder" : {
            "id": "PO1234",
            "name": "ASP Billing",
            "date": "25/03/2017",
            "usage": 50000.0,
            "amountLeft": 20000.0
          }},
          { "purchaseOrder" : {
            "id": "PO4567",
            "name": "TYME Vault",
            "date": "25/04/2017",
            "usage": 50000.0,
            "amountLeft": 20000.0
          }},
          { "purchaseOrder" : {
            "id": "PO1357",
            "name": "ASP Billing",
            "date": "25/03/2017",
            "usage": 50000.0,
            "amountLeft": 20000.0
          }},
          { "purchaseOrder" : {
            "id": "PO2468",
            "name": "TYME Vault",
            "date": "25/03/2017",
            "usage": 50000.0,
            "amountLeft": 20000.0
          }}
        ]
      }};
    this.setState({ data: data });
  }

  render() {
    let invoice = this.state.data.invoice;
    return(
      <div className="section">
        <Link className="IND-backText" to="/home/invoices">
          <img src={backIcon} className="IND-backIcon" />BACK
        </Link>
        <div className="sectionTitle">Invoice Details</div>
        <div className="IND-details">
          <div className="IND-detail">
            <div className="IND-detail-label">
              Name:
            </div>
            <div className="IND-detail-value">
              {invoice.name}
            </div>
          </div>
          <div className="IND-detail">
            <div className="IND-detail-label">
              Invoice #:
            </div>
            <div className="IND-detail-value">
              {invoice.invoiceId}
            </div>
          </div>
          <div className="IND-detail">
            <div className="IND-detail-label">
              Date:
            </div>
            <div className="IND-detail-value">
              {invoice.date}
            </div>
          </div>
          <div className="IND-detail">
            <div className="IND-detail-label">
              Status:
            </div>
            <div className="IND-detail-value">
              {invoice.status}
            </div>
          </div>
          <div className="IND-detail"></div>
        </div>
        <table className="IND-table">
          <tbody>
            <tr className="IND-tableHeaders">
              <th className="IND-tableHeader">PO #</th>
              <th className="IND-tableHeader">Name</th>
              <th className="IND-tableHeader">Date</th>
              <th className="IND-tableHeader">Total Usage</th>
              <th className="IND-tableHeader">Amount Left</th>
            </tr>
            {
              invoice.purchaseOrders.map(function(d, i) {
                return <tr className="IND-tableRows" key={i}>
                    <td className="IND-tableRow">{d.purchaseOrder.id}</td>
                    <td className="IND-tableRow">{d.purchaseOrder.name}</td>
                    <td className="IND-tableRow">{d.purchaseOrder.date}</td>
                    <td className="IND-tableRow">{d.purchaseOrder.usage}</td>
                    <td className="IND-tableRow">{d.purchaseOrder.amountLeft}</td>
                  </tr>
              })
            }
            <tr className="IND-tableRows SOD-totalRow">
              <td className="IND-tableRow"></td>
              <td className="IND-tableRow">TOTAL</td>
              <td className="IND-tableRow"></td>
              <td className="IND-tableRow">50</td>
              <td className="IND-tableRow">4000</td>
            </tr>
          </tbody>
        </table>
      </div>
    )
  }
}

export default InvoiceDetails;



