import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  NavLink
} from 'react-router-dom';
import './Invoices.css';
import detailsIcon from '../../../assets/details.png';
import InvoiceDetails from './InvoiceDetails/InvoiceDetails';

class Invoices extends React.Component {
  constructor() {
    super();
    this.state = {
      data: []
    };
  }

  componentWillMount() {
    //TODO: AJAX Call to fetch invoices
    let data = [
      { "invoice" : {
        "invoiceId": "IN123",
        "name": "ABC Consulting",
        "date": "25/03/2017",
        "status": "Processed"
      }},
      { "invoice" : {
        "invoiceId": "IN123",
        "name": "ABC Consulting",
        "date": "25/03/2017",
        "status": "Processed"
      }}
    ];
    this.setState({ data: data });
  }

  render() {
    console.log(this.state.data);
    return(
      <div className="section">
        <div className="sectionTitle">Invoices</div>
        <table className="IN-table">
          <tbody>
            <tr className="IN-tableHeaders">
              <th className="IN-tableHeader">Invoice #</th>
              <th className="IN-tableHeader">Name</th>
              <th className="IN-tableHeader">Date</th>
              <th className="IN-tableHeader">Status</th>
              <th className="IN-tableHeader"></th>
            </tr>
            {
              this.state.data.map(function(d, i) {
                let invoiceId = d.invoice.invoiceId;
                return <tr className="IN-tableRows" key={i}>
                    <td className="IN-tableRow">{d.invoice.invoiceId}</td>
                    <td className="IN-tableRow">{d.invoice.name}</td>
                    <td className="IN-tableRow">{d.invoice.date}</td>
                    <td className="IN-tableRow">{d.invoice.status}</td>
                    <td className="IN-tableRow">
                      <div className="IN-details">
                        <img src={detailsIcon} className="IN-detailsIcon" />
                        <NavLink to={'invoices/'+invoiceId} className="IN-detailsText">DETAILS</NavLink>
                      </div>
                    </td>
                  </tr>
              })
            }
          </tbody>
        </table>
      </div>
    )
  }
}

export default Invoices;



