import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Link,
  NavLink
} from 'react-router-dom';

import backIcon from '../../../../assets/back.png';
import approvedIcon from '../../../../assets/approved.png';
import pendingIcon from '../../../../assets/pending.png';
import onholdIcon from '../../../../assets/onhold.png';

import addIcon from '../../../../assets/add.png';
import checkIcon from '../../../../assets/check.png';
import clearIcon from '../../../../assets/clear.png';

import './CreateServiceOrder.css';

class CreateServiceOrder extends React.Component {
  constructor() {
    super();
    this.row = {
      "role": "",
      "roleType": "",
      "resource": "",
      "hours": 0,
      "cost": 0,
      "total": 0
    };

    this.state = {
      "serviceOrderId": "",
      "purchaseOrderId": "",
      "supplier": "",
      "supplierId": "",
      "projectName": "",
      "pricingType": "",
      "ppmcCode": "",
      "projectManager": "",
      "price": "",
      "estimatedCharges": [],
      "row": {
        "role": "",
        "roleType": "",
        "resource": "",
        "hours": '',
        "cost": '',
        "total": ''
      },
      "data": {}
    };

    this.handleSupplierChange = this.handleSupplierChange.bind(this);
    this.handlePricingTypeClick = this.handlePricingTypeClick.bind(this);
    this.handleProjectNameChange = this.handleProjectNameChange.bind(this);
    this.handlePpmcCodeChange = this.handlePpmcCodeChange.bind(this);
    this.handleProjectManagerChange = this.handleProjectManagerChange.bind(this);
    this.handleRoleChange = this.handleRoleChange.bind(this);
    this.handleRoleTypeChange = this.handleRoleTypeChange.bind(this);
    this.handleHoursChange = this.handleHoursChange.bind(this);
    this.handleResourceNameChange = this.handleResourceNameChange.bind(this);
    this.handleAddResourceClick = this.handleAddResourceClick.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentWillMount() {
    // let data = $.ajax({
    //   type: 'GET',
    //   url: '/api/v1/retrieve_info/',
    // }).done((response) => {
    //   return response.json;
    // }).then((data) => {
    //   this.setState({ prices: JSON.parse(data.body) });
    // });
    // let peers = $.ajax({
    //   type: 'GET',
    //   url: '/api/v1/peers/',
    // }).done((response) => {
    //   return response.json;
    // }).then((data) => {
    //   this.setState({ peers: JSON.parse(data.body) });
    // });

    this.initializeData();
  }

  initializeData() {
    /** For J's testing only **/
    let prices = {
      "offShorePrices" : {
        "Java Developer" : "80000",
        "Frontend Developer" : "60000",
        "Backend Developer" : "60000"
      },
      "onShorePrices" : {
        "Java Developer" : "100000",
        "Frontend Developer" : "75000",
        "Backend Developer" : "75000"
      }
    };

    let peers = ["DXC", "TCS"];

    let projectManagers = [
      {
        id: 1,
        name: "Peter Smith"
      },
      {
        id: 2,
        name: "Allison Jackson"
      }
    ];

    let pricingType = [
      {
        id: 1,
        code: "FIXED_PRICE",
        name: "Fixed Price"
      },
      {
        id: 2,
        code: "ESTIMATED_PRICE",
        name: "Estimated Price"
      }
    ];

    let roles = Object.keys(prices.offShorePrices);

    let roleTypes = [
      {
        id: 1,
        name: "Onshore"
      },
      {
        id: 2,
        name: "Offshore"
      }
    ];

    let resources = [
      {
        id: 1,
        name: "Michael Jordan"
      },
      {
        id: 2,
        name: "Kobe Bryant"
      },
      {
        id: 3,
        name: "LeBron James"
      }
    ];

    let sampleData = {
      prices: prices,
      peers: peers,
      projectManagers: projectManagers,
      pricingType: pricingType,
      roles: roles,
      roleTypes: roleTypes,
      resources: resources,
    }

    this.setState({ data: sampleData });
  }

  updateCost() {
    var row = this.state.row;
    var data = this.state.data;
    console.log("role - " + row.role);
    console.log("roleType - " + row.roleType);
    console.log("hours - " + row.hours);
    if (row.role && row.roleType && row.hours && parseInt(row.hours) > 0) {
      var onshore = (data.roleType === "Onshore") ? true : false;
      var price = onshore ? data.prices.onShorePrices[row.role] : data.prices.offShorePrices[row.role];
      var total = parseInt(row.hours) * parseInt(price);
      console.log("total: " + total);
      this.state.row.total = total;
      this.forceUpdate();
    }
  }

  updateTotalCost() {
    var totalPrice = 0;
    for (var j = 0; j < this.state.estimatedCharges.length; j++) {
        totalPrice += this.state.estimatedCharges.total;
    }
    this.setState({ price: totalPrice });
    this.setState(this.state.tableData);
  }

  handleSupplierChange(e) {
    this.setState({ supplier: e.target.value });
    this.setState({ supplierId: e.target.value });
  }

  handleProjectNameChange(e) {
    this.setState({ projectName: e.target.value });
  }

  handlePricingTypeClick(e) {
    this.setState({ pricingType: e.target.value });
  }

  handlePpmcCodeChange(e) {
    this.setState({ ppmcCode: e.target.value });
  }

  handleProjectManagerChange(e) {
    this.setState({ projectManager: e.target.value });
  }

  handleRoleChange(e) {
    let role = e.target.value;
    let row = {
      "role": role,
      "roleType": this.state.row.roleType,
      "resource": this.state.row.resource,
      "hours": this.state.row.hours,
      "cost": this.state.row.cost,
      "total": this.state.row.total
    }
    this.setState({ row : row }, function() {
      this.updateCost();
    });
  }

  handleRoleTypeChange(e) {
    let roleType = e.target.value;
    let row = {
      "role": this.state.row.role,
      "roleType": roleType,
      "resource": this.state.row.resource,
      "hours": this.state.row.hours,
      "cost": this.state.row.cost,
      "total": this.state.row.total
    }
    this.setState({ row : row }, function() {
      this.updateCost();
    });
  }

  handleHoursChange(e) {
    let hours = e.target.value;
    let row = {
      "role": this.state.row.role,
      "roleType": this.state.row.roleType,
      "resource": this.state.row.resource,
      "hours": hours,
      "cost": this.state.row.cost,
      "total": this.state.row.total
    }
    this.setState({ row : row }, function() {
      this.updateCost();
    });
  }

  handleResourceNameChange(e) {
    let resource = e.target.value;
    let row = {
      "role": this.state.row.role,
      "roleType": this.state.row.roleType,
      "resource": resource,
      "hours": this.state.row.hours,
      "cost": this.state.row.cost,
      "total": this.state.row.total
    }
    this.setState({ row : row });
  }

  handleAddResourceClick(e) {
    if (this.state.row.role !== '' && this.state.row.roleType !== '' && this.state.row.resource !== '' && this.state.row.hours !== '') {
      this.state.estimatedCharges.push(this.state.row);
      let emptyRow = {
        "role": "",
        "roleType": "",
        "resource": "",
        "hours": '',
        "cost": '',
        "total": ''
      };
      this.setState({row: emptyRow}, function() {
        console.log(this.state);
      });
    } else {
      alert('Incomplete details.');
    }
  }

  handleRemove() {

  }

  handleSubmit() {
    //Process submit here, then go to location
    window.location = "/home/service_orders";
  }

  render() {
    let noData = (this.state.estimatedCharges.length < 1) ? <div className="CSOD-noData">No entries added yet.</div> : "";
    return(
      <div className="CSOD section">
        <Link className="SOD-backText" to="/home/service_orders">
          <img src={backIcon} className="SOD-backIcon" />BACK
        </Link>
        <div className="sectionTitle">New Service Order</div>
        <div className="CSOD-details">
          <div className="CSOD-detail">
            <div className="CSOD-label">Project Name:</div>
            <input type="text" onChange={this.handleProjectNameChange} />
          </div>
          <div className="CSOD-detail">
            <div className="CSOD-label">PPMC Code:</div>
            <input type="text" onChange={this.handlePpmcCodeChange} />
          </div>
          <div className="CSOD-detail">
            <div className="CSOD-label">Supplier:</div>
            <select defaultValue="" className="CSOD-select" onChange={this.handleSupplierChange}>
              <option disabled value="">Select</option>
              {
                this.state.data.peers.map((peer) =>
                  <option key={peer} value={peer}>{peer}</option>
                )
              }
            </select>
          </div>
          <div className="CSOD-detail">
            <div className="CSOD-label">Pricing Type:</div>
            <select defaultValue="" className="CSOD-select" onChange={this.handlePricingTypeClick}>
              <option disabled value="">Select</option>
              {
                this.state.data.pricingType.map((pt) =>
                  <option key={pt.id} value={pt.code}>{pt.name}</option>
                )
              }
            </select>
          </div>
          <div className="CSOD-detail">
            <div className="CSOD-label">Project Manager:</div>
            <select defaultValue="" className="CSOD-select" onChange={this.handleProjectManagerChange}>
              <option disabled value="">Select</option>
              {
                this.state.data.projectManagers.map((pm) =>
                  <option key={pm.id} value={pm.name}>{pm.name}</option>
               )}
            </select>
          </div>
        </div>
        <table className="CSOD-table">
          <tbody>
            <tr className="CSOD-tableHeaders">
              <th className="CSOD-tableHeader">Role Description</th>
              <th className="CSOD-tableHeader">Role Type</th>
              <th className="CSOD-tableHeader">Resource Name</th>
              <th className="CSOD-tableHeader">Estimated Hours</th>
              <th className="CSOD-tableHeader">Total Cost</th>
              <th className="CSOD-tableHeader">Actions</th>
            </tr>
            <tr>
              <td className="CSOD-tableRow">
                <select value={this.state.row.role}  className="CSOD-select" onChange={this.handleRoleChange}>
                  <option disabled value="">Select</option>
                  {
                    this.state.data.roles.map((role) =>
                      <option key={role} value={role}>{role}</option>
                    )
                  }
              </select>
              </td>
              <td className="CSOD-tableRow">
                <select value={this.state.row.roleType}  className="CSOD-select" onChange={this.handleRoleTypeChange}>
                  <option disabled value="">Select</option>
                  {
                    this.state.data.roleTypes.map((role) =>
                      <option key={role.id} value={role.name}>{role.name}</option>
                    )
                  }
                </select>
              </td>
              <td className="CSOD-tableRow">
              <select value={this.state.row.resource} className="CSOD-select" onChange={this.handleResourceNameChange}>
                <option disabled value="">Select</option>
                {
                  this.state.data.resources.map((resource) =>
                    <option key={resource.id} value={resource.name}>{resource.name}</option>
                  )
                }
              </select>
              </td>
              <td className="CSOD-tableRow">
                <input value={this.state.row.hours} className="CSOD-inputHours" type="number" onChange={this.handleHoursChange} />
              </td>
              <td className="CSOD-tableRow">
                <input type="text" value={this.state.row.total} className="CSOD-inputHours" disabled />
              </td>
              <td className="CSOD-tableRow">
                <img src={checkIcon} className="CSOD-addIcon" onClick={this.handleAddResourceClick}/>
              </td>
            </tr>
          </tbody>
        </table>
        <table className="CSOD-table2">
          <tbody>
            <tr className="CSOD-tableHeaders">
              <th className="CSOD-tableHeader">Role Description</th>
              <th className="CSOD-tableHeader">Role Type</th>
              <th className="CSOD-tableHeader">Resource Name</th>
              <th className="CSOD-tableHeader">Estimated Hours</th>
              <th className="CSOD-tableHeader">Total Cost</th>
              <th className="CSOD-tableHeader">Actions</th>
            </tr>
              {
                this.state.estimatedCharges.map((row) =>
                <tr className="CSOD-tableRowList">
                  <td className="CSOD-tableRow">{row.role}</td>
                  <td className="CSOD-tableRow">{row.roleType}</td>
                  <td className="CSOD-tableRow">{row.resource}</td>
                  <td className="CSOD-tableRow">{parseFloat(row.hours)}</td>
                  <td className="CSOD-tableRow">{parseFloat(row.total)}</td>
                  <td className="CSOD-tableRow">
                    <img src={clearIcon} className="CSOD-addIcon"/>
                  </td>
                </tr>
                )
              }
          </tbody>
        </table>
        { noData }
        <div className="CSOD-submitSo" onClick={this.handleSubmit}>
          Submit
        </div>
      </div>
    )
  }
}

export default CreateServiceOrder;



