import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom';
import logo from '../../assets/commbank_logo.svg';
import userIcon from '../../assets/user.png';

import './Login.css';

class Login extends React.Component {
  constructor() {
    super();
    this.state = {
      username: '',
      password: ''
    }
    this.handleLoginClick = this.handleLoginClick.bind(this);
    this.handleUsernameChange = this.handleUsernameChange.bind(this);
    this.handlePasswordChange = this.handlePasswordChange.bind(this);
  }

  handleLoginClick(e) {
    console.log(this.state.username + " : " + this.state.password);
  }

  handleUsernameChange(e) {
    this.setState({ username: e.target.value });
  }

  handlePasswordChange(e) {
    this.setState({ password: e.target.value });
  }

  render() {
    return (
      <div className="Login">
        <div className="Login-navbar">
          <img src={logo} className="App-logo" alt="logo" />
        </div>
        <div className="Login-body">
          <div className="Login-form">
            <form id="login" action="/login" method="post">
              <img src={userIcon} className="Login-userIcon" alt="userIcon" /><br />
              <div className="Login-label">
                Username
              </div>
              <input name="username" className="textbox" type="text" placeholder="username" /><br />
              <div className="Login-label">
                Password
              </div>
              <input name="password" className="textbox" type="password" placeholder="password" />
              <div className="Login-bottom">
                <div className="Login-forgotPassword">
                  Forgotten your password?
                </div>
                <input type="submit" className="Login-button" value="Submit" />
              </div>
            </form>
          </div>
        </div>
      </div>
    )
  }
}

export default Login;



